import React, { Component, Fragment } from 'react';
import { Form, Col, Input } from 'antd';

export default class StudentCodeNameInput extends Component {
  state = {};
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
        <Col xs={24} sm={12} md={6}>
          <Form.Item label="M SV">{getFieldDecorator('studentCode', {})(<Input />)}</Form.Item>
        </Col>
        <Col xs={24} sm={12} md={6}>
          <Form.Item label="Tn SV">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
        </Col>
      </Fragment>
    );
  }
}



