import { request, requestDownload } from 'utils';

// request.prefix = '/api/students';
const prefix = '/api/students';

export const getPrizeNotes = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  studentCode,
  studentFullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/PrizeNote/GetPrizeNoteStudents',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      studentCode,
      studentFullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const deletePrizeNotes = id =>
  request({
    prefix,
    url: `/PrizeNote/${id}`,
    method: 'POST',
  });

// export const createprizeNotes = ({ id, approvalNumber, approvalDate, description, pnDetails }) =>
//   request({
//     url: `/PrizeNote/CreatePNNoteAndDetail`,
//     data: { id, approvalNumber, approvalDate, description, pnDetails },
//     method: 'POST',
//   });

export const createprizeNotes = data =>
  request({
    prefix,
    url: `/PrizeNote/CreatePNNoteAndDetail`,
    data,
    method: 'POST',
  });

export const exportPrizeNotes = ({ studyFieldId, semesterId, yearEduId }) =>
  requestDownload({
    defaultFileName: 'File_PrizeNoteStudents.xlsx',
    prefix,
    url: `/Student/ExportPrizeNoteStudents`,
    method: 'POST',
    data: { studyFieldId, semesterId, yearEduId },
  });

// =============

export const getCodeStudent = id =>
  request({
    prefix,
    url: `/Student/GetByCode/${id}`,
    method: 'GET',
  });
export const getBonusType = () =>
  request({
    prefix: '/api/categories',
    url: `/BonusType`,
    method: 'GET',
  });
export const getBonusTypeChild = id =>
  request({
    prefix: '/api/categories',
    url: `/BonusType/GetBonusTypeChild`,
    params: {
      id,
    },
    method: 'GET',
  });

export const addItemAddReward = dataAddReward =>
  request({
    prefix,
    url: `/BonusNote/CreateBNNoteAndDetail`,
    data: {
      ...dataAddReward,
    },
    method: 'POST',
  });

export const getListAddReward = params =>
  request({
    prefix,
    url: `/BonusNote/GetBNNoteStudents`,
    params: {
      ...params,
    },
    method: 'GET',
  });

export const exportDataAddReward = params => {
  console.log('params', params);
  return requestDownload({
    prefix,
    url: `/BonusNote/ExportBonusNote`,
    data: {
      ...params,
    },
    method: 'POST',
  });
};

export const getAddRewardById = id =>
  request({
    prefix,
    url: `/BonusNote/${id}`,
    method: 'GET',
  });



