import React, { Component, Fragment } from 'react';
import { BtnEdit, BtnDelete } from 'components/common/button';
import { Popconfirm } from 'antd';

export default class ColEditDelete extends Component {
  sate = {};
  render() {
    return (
      <Fragment>
        <BtnEdit href={this.props.hrefEdit} />
        <Popconfirm
          title="ng  xa ?"
          onConfirm={this.props.onConfirmDelete}
          okText="Xa"
          cancelText="Hy"
        >
          <BtnDelete />
        </Popconfirm>
      </Fragment>
    );
  }
}



